
.toDoPageDiv {
    position: relative;
    display: flex;
    flex-direction: column;
    flex: 1;
    border-radius: 1em 1em 4px 4px;
    border: 2px solid #c0e0ff;
    margin: 5px;
    height: calc(100% - 10px);
}

.todoTitleDiv {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-basis: 48px;
    color: #4f4f4f;
    font-size: 32px;
    border-bottom: 2px solid #c0e0ff;
}

.todoAddTaskBtn {
    position: absolute;
    right: 32px;
    bottom: 24px;
    background: none;
    border: 0;
    width: 48px;
    height: 48px;
}